
import axios from 'axios'
import XHeader from 'vux/src/components/x-header/index.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
XHeader,
GroupTitle,
Flexbox,
FlexboxItem,
XButton,
Alert,
Tab,
TabItem,
},
filters: {
formatDuring: function (value) {
if(value == "" || value == null || value == undefined){
var value="无"
return value

}else {
Date.prototype.toLocaleString = function(){
let months = this.getMonth()+1;
if(months < 10){
months = '0' + months;
}
let dates = this.getDate();
if(dates < 10){
dates = '0' + dates;
}
let hours = this.getHours();
if(hours < 10){
hours = '0' + hours;
}
let minutes = this.getMinutes();
if(minutes < 10){
minutes = '0' + minutes;
}
return this.getFullYear() +'.'+months+'.'+dates+' '+hours+':'+minutes
}
return new Date(value).toLocaleString();
}}
},
data() {
return {
departmentid:this.$store.getters.user.departmentid,
active : [],
unactive : [],
show:false,
msg:'',
userId:cookie.get('userId'),
isActive:false,
infoM:[],
tabIndex:"1",
activeCount : 0,
unactiveCount : 0
}
},
methods:{
getActivityMore(){
axios({
method: 'get',
url: 'active/getParticipateActive',
params: {
pageNum:1,
pageSize:200,
departmentid:this.departmentid,
userId:this.$store.getters.user.userid
}
}).then((res)=> {
res.data.list.forEach(element => {
if(element.signupstatus == 1)
{
this.active.push(element);
}
else
{
this.unactive.push(element);
}
this.infoM = this.active;
this.activeCount = this.active.length;
this.unactiveCount = this.unactive.length;
});

}).catch(function (error) {
console.log(error);
});
},
submit(id){
axios({
method: 'post',
url: 'active/participate',
params: {
userId:this.userId,
activeId:id,
departmentid:this.departmentid
}
}) .then((res)=> {
this.msg=res.msg,
this.show=true
})
.catch(function (error) {
console.log(error);
});
},
submit1(id){
this.$vux.alert.show({title:'请勿重复报名'});
},
onHide () {
window.location.reload()
},
onShow () {
console.log('on show')
},
datePick(s){
Date.prototype.toLocaleString = function(){
return this.getFullYear() +'.'+ (this.getMonth()+1)+'.'+this.getDate()
}
return new Date(s).toLocaleString();
},
toggle(){

this.infoM = [];
this.infoM = this.active;

},
noSign(){

this.infoM = [];
this.infoM = this.unactive;
},
} ,
mounted() {
this.getActivityMore();
console.log(this.unactive);

}
}



